<template>
  <page
    @on-ready="onReady"
    :headerHeightVW="headerHeightVW"
    :scrollbar="true"
    ref="page"
    class="taizhang-page"
  >
    

    <template slot="header">
      <van-nav-bar id="header" :title="title" :left-arrow="(types && types.length) ? true : false" @click-left="onClickLeft">
        <i class="icon-wode nav-icon-right" slot="right" @click="handleToMe" v-if="(types && types.length) ? false : true"></i>
      </van-nav-bar>

      <van-tabs v-model="active" class="taizhang-tabs" v-if="!types.length">
        <van-tab title="位置树"></van-tab>
        <van-tab title="类别树"></van-tab>
      </van-tabs>
    </template>

    <div class="list-container">
      <van-cell-group>
        <van-cell v-if="typeShow"><span style="color: rgba(0,0,0,0.5);" v-html="typeShow"></span></van-cell>
      </van-cell-group>
      
      <van-cell-group>
        <van-radio-group v-model="radio">
        <van-cell @click="handleType(item)" v-for="item in typeList" :key="item.id"  is-link clickable>
          <!-- <van-button type="primary" size="mini" color="#1b82d2">选择</van-button> -->
          <!-- <van-radio slot="icon" :name="item.id" @click.stop="handleTypeSelect(item)">&nbsp;&nbsp;</van-radio> -->
          <span>{{item.name}}</span>
        </van-cell>
        </van-radio-group>
      </van-cell-group>     

      <van-cell-group>
        <van-cell @click="handleDevice(item)" v-for="item in deviceList" :key="item.id" :title="item.name"  clickable>
        </van-cell>
      </van-cell-group>
    </div>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 0,
    deviceList: [],
    typeList: [],
    types: [],
    id: null,
    mode: null,
    radio: null,
    title: '智能维修',
    active: 0
  }),
  computed: {
    typeShow () {
      let strArray = []
      if (this.types) {
        for (let i = 0; i < this.types.length; i++) {
          strArray.push(this.types[i].name)
        }
      }
      
      strArray[strArray.length - 1] = `<span style="color: #108EE9;">${strArray[strArray.length - 1]}</span>`
      return strArray.join(' > ')
    }
  },
  methods: {
    onReady (header, main) {

      if (navigate.getParam('types')) {
        this.types = navigate.getParam('types')
      }

      if (navigate.getParam('active')) {
        this.active = navigate.getParam('active')
      }

      this.headerHeightVW = (header.h + (this.types.length ? 1 : 45)) * 100 / header.w

      this.loadList()
    },
    loadList () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      io.getDevice({
        type: this.types.length > 0 ? this.types[this.types.length - 1].id : 0,
        mode: this.active == 0 ? 'address': 'category'
      }).then(data => {
        this.typeList = data.typeList
        this.deviceList = data.deviceList
        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    handleType (item) {
      let typesParam = [...this.types]
      typesParam.push(item)

      let cid = []

      if (navigate.getParam('cid')) {
        cid = navigate.getParam('cid')
      }

      cid.push(item.id)

      navigate.copen('taizhang', item.id, {active: this.active, types: typesParam, radom: (new Date()).getTime(), cid })
    },

    handleDevice (item) {

      navigate.open('equipment_details', {id: item.id})
    },

    onClickLeft () {
      navigate.back()
    },

    handleToMe () {
      navigate.open('me')
    }
  },
  watch: {
    active (val) {
      this.loadList()
    }
  }
}
</script>

